.product h1 {
  display: flex;
  justify-content: center;
  padding-top: 10%;
  text-transform: uppercase;
  font-size: 4em;
  font-weight: lighter; }

.container0 {
  background: #000000;
  height: 90vh; }

.hover {
  position: relative;
  top: 50%;
  left: 40%;
  transform: translate(-50%, -50%);
  text-decoration: none;
  color: #fff;
  font-size: 3em;
  border: 2px solid #cecdcd;
  padding: 20px 30px; }

.hover:hover {
  background: #fff;
  color: #444;
  transition: 6s; }
